@use '../util' as u;

.topnav {
	background-color: var(--header-bg);

	&__wrapper {
		// Grid styles
		// display: grid;
		// grid-template-columns: repeat(2, auto);
		// justify-content: space-between;
		// align-items: center;
		// Flexbox styles
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding-block: 12px;

		@include u.breakpoint(large) {
			padding-block: 20px;
		}
	}

	&__homelink {
		&:hover {
			opacity: 0.9;
		}
	}

	&__logo {
		width: u.rem(70);

		@include u.breakpoint(large) {
			width: u.rem(128);
		}
	}

	&__links {
		// Grid styles
		// display: grid;
		// grid-template-columns: repeat(3, auto);
		// gap: 40px;
		// Flexbox styles
		display: flex;
		flex-direction: row;
		align-items: center;
		gap: 20px;
		margin: 0;
		padding: 0;

		@include u.breakpoint(large) {
			gap: 40px;
		}
	}

	&__item {
		list-style-type: none;
	}

	&__link {
		font-size: u.rem(14);
		font-weight: 700;
		letter-spacing: 0.05em;
		color: var(--text-light);
		text-transform: uppercase;

		@include u.breakpoint(large) {
			font-size: u.rem(16);
		}

		&:hover {
			text-decoration: underline;
			text-decoration-thickness: 2px;
			text-decoration-color: var(--text-light);
			text-underline-offset: 4px;
		}
	}
}
