@use '../util' as u;

h1,
h2,
h3 {
	font-weight: 700;
	margin-top: 0;
}

h1 {
	font-size: u.rem(42); //2.625rem;
	font-size: clamp(
		u.rem(42),
		1.77rem + 3.64vw,
		u.rem(72)
	); // 2.625rem <-> 4.5rem
	line-height: 1;
	margin-block-end: u.rem(20);
}

h2 {
	font-size: u.rem(36); // 2.25rem
	font-size: clamp(u.rem(36), 1.91rem + 1.45vw, u.rem(48)); // 2.25rem <-> 3rem
}

h3 {
	font-size: u.rem(24);
}

p {
	font-size: u.rem(18);
	line-height: 1.3;
	margin-top: 0;

	&.medium {
		font-size: u.rem(20);
		font-size: clamp(1.25rem, 1.14rem + 0.48vw, 1.5rem);
	}

	&.large {
		font-size: u.rem(24);
		font-size: clamp(u.rem(24), 1.39rem + 0.48vw, u.rem(28));
		margin-block-end: u.rem(40);
	}
}

a,
a:visited,
a:active {
	text-decoration: none;
}
